@font-face {
    font-family: "Roboto-Medium";
    src: url("../fonts/Roboto-Medium.eot"), url("../fonts/Roboto-Medium.woff2"), url("../fonts/Roboto-Medium.woff"), url("../fonts/Roboto-Medium.ttf"), url("../fonts/Roboto-Medium.svg");
}

@font-face {
    font-family: "Roboto-Regular";
    src: url("../fonts/Roboto-Regular.eot"), url("../fonts/Roboto-Regular.woff2"), url("../fonts/Roboto-Regular.woff"), url("../fonts/Roboto-Regular.ttf"), url("../fonts/Roboto-Regular.svg");
}
* {
    margin: 0;
    padding: 0;
}
body {
    background-color: rgb(180, 180, 180);
    height: 100vh;
}
.conteiner {
    max-width: 850px;
    margin: 0 auto;
    padding: 2rem 3rem 0 3rem;
    background-color: rgb(255, 255, 255);
    height: 100vh;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: "Roboto-Regular";
}
.log-in {
    display: flex;
    &__title {
        margin: 0;
        background-color: rgb(0, 174, 219);
        color: rgb(255, 255, 255);
        padding: 5px;
    }
}
.style-all {
    margin-right: 5px;
    font-size: 16px;
    &:last-child {
        margin: 0;
    }
}
.title {
    display: flex;
    color: rgb(255, 255, 255);
    font-family: "Roboto-Medium";
    font-size: 24px;
}
.one {
    background-color: rgb(0, 174, 219);
    padding: 5px;
}
.two {
    background-color: rgb(181, 218, 6);
    padding: 5px;
}
.create {
    &__link {
        text-decoration: none;
        background-color: rgb(181, 218, 6);
        color: rgb(255, 255, 255);
        padding: 10px 15px;
    }
}
.main {
    font-family: "Roboto-Medium";
    max-width: 850px;
    margin: 0 auto;
    margin: 3rem 0 0 0;
    position: relative;
    &__ad {
        &__description {
            font-family: "Roboto-Regular";
        }
        &__title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            &__inner{
                font-family: "Roboto-Medium";
                margin: 0;
                text-decoration: none;
                font-size: 32px;
                color: rgb(0, 0, 0);
            }
            &__delete,
            &__edit {
                height: 24px;
                padding: 0 8px;
                background-color: rgb(0, 174, 219);
                color: rgb(255, 255, 255);
                border: none;
                cursor: pointer;
            }
        }
        &__author {
            margin: 0 0 1rem 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &__title {
                text-decoration: none;
                color: rgb(0, 174, 219);
            }
        }
        &__datatime {
            color: rgb(255, 255, 255);
            position: absolute;
            top: 1rem;
            left: -100px;
            &__data {
                background-color: rgb(247, 143, 29);
                padding: 3px;
            }
            &__time {
                background-color: rgb(0, 174, 219);
                padding: 3px;
            }
        }
    }
}

.form__create {
    display: flex;
    flex-direction: column;
    max-width: 450px;
    margin: 0 auto;
    font-family: "Roboto-Medium";
    &__title {
        text-align: center;
        background-color: rgb(0, 174, 219);
        color: rgb(255, 255, 255);
        padding: 10px 0;
    }
}
.create__ad {
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
    &__title {
        margin-bottom: 1rem;
    }
    &__input {
        padding: 10px 5px;
        margin-bottom: 3rem;
    }
    &__description {
        margin-bottom: 1rem;
    }
    &__button {
        display: flex;
        justify-content: center;
        margin-top: 3rem;
        &__submit {
            padding: 10px 15px;
            background-color: rgb(247, 143, 29);
            border: none;
            color: rgb(255, 255, 255);
        }
    }
}
#description {
    padding: 10px 5px;
}